import React from 'react';

import { TextButtonWrapper } from './styled';

interface TextButtonProps {
  name: string | undefined;
  handleClick: (event: any) => any;
  testid: string;
  role: string;
}

const TextButton: React.FC<TextButtonProps> = (props) => {
  const { name, handleClick ,testid, role } = props;
  return (
    <TextButtonWrapper
      data-testid={testid}
      onClick={handleClick}
      data-role={role}
    >
      {name}
    </TextButtonWrapper>
  );
};

export default TextButton;
